#notfound {
    position: relative;
    height: 100vh;
    background: #ffff;
  }
  
  #notfound .notfound {
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
  }
  
  .notfound {
    max-width: 767px;
    width: 100%;
    line-height: 1.4;
    padding: 110px 40px;
    text-align: center;
    // background: #1D1D1D;
    // -webkit-box-shadow: 0 15px 15px -10px rgba(0, 0, 0, 0.1);
            // box-shadow: 0 15px 15px -10px rgba(0, 0, 0, 0.1);
  }
  
  .notfound .notfound-404 {
    position: relative;
    height: 180px;
  }
  
  .notfound .notfound-404 h1 {
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    margin: 0px;
  }
  
  .notfound h2 {
    margin-top: 0px;
    margin-bottom: 25px;
  }
  
